.form {
 @include pageGrid;

 &__title {
  grid-column: 1 / -1;

  @include onTablet {
    grid-column: 1 / 6;
  }
 }

 &__content {
  grid-column: 1 / -1;

  @include onTablet {
    grid-column: 1 / 5;
  }

  @include onDesktop {
    grid-column: 7 / 13;
  }
 }

 &__form {
  margin-top: 30px;
  grid-column: 1 / -1;

  @include onTablet {
    grid-column: 1 / 4;
  }
 }

 &__input {
  width: 100%;
  border: none;
  border-bottom: 1px solid $color-text;
  margin-bottom: 30px;
 }

 &__btn {
  grid-column: 1 /-1;

  @include onTablet {
    grid-column: 1 / 3;
  }
 }
}
